<template>
<div class="improper-evidence-list-wrap">
    <div class="list-header">
        <el-radio-group v-model="selectedType" size="medium">
      <el-radio-button class="custom-radio-button" v-for="item in typeList" :label="item.cName" :key="item.id">{{item.cName}}</el-radio-button>
    </el-radio-group>
    </div>

    <div class="list-body">
    <dl class="improper-evidence-item" v-for="item in evidenceList" :key="item.evidenceAnalysisImproperId">
        <dt>
            <span class="title">证据内容</span>
            <span class="op-btns-wrap">
                <el-button type="text" @click="switchPdfByOcrfileId(item.ocrFileId)">查看</el-button>
                <el-button type="text" @click="editImproperEvidence(item)">编辑</el-button>
                <el-button type="text" @click="deleteImproperEvidence(item.evidenceAnalysisImproperId)">删除</el-button>
            </span>
        </dt>
        <dd class="text">{{item.improperEvidenceContent}}</dd>
        <dt><span class="title">{{item.improperEvidenceName}}描述</span></dt>
        <dd class="desc-item" v-for="subItem in item.caseMgtImproperEvidenceCheckboxVoList" :key="subItem.$index"><i></i>{{subItem.improperEvidenceCheckboxName}}</dd>
    </dl>

        <div v-if="evidenceList.length == 0" class="no-result">
            <img src="../assets/img/no_result.png" alt="">
            <p style="text-align: center;">该列表暂无数据！</p>
        </div>
    </div>
</div>
</template>
<script>
import {
  getImproperEvidenceType,
  getImproperEvidenceListByCaseNumber,
  deleteImproperEvidence
} from '@/api/root'

import EE from 'events'
import _ from 'lodash'

let ImproperEvidenceAddedCb
let originList = []
export default {
  props: {
    caseObj: {
      type: Object,
      required: true
    }
  },
  name: 'improperEvidenceList',
  data() {
    return {
      selectedType: '',
      typeList: [],
      evidenceList: []
    }
  },
  computed: {},
  components: {},
  methods: {
    // 通过ocrfileId查看对应pdf文件
    switchPdfByOcrfileId(ocrfileId) {
      if (!ocrfileId || ocrfileId === '') {
        this.$message.error('该条记录的原始文档ocrfileId为空')
      }
      this.ee.emit('switchPdfByOcrfileIdEvent', ocrfileId)
    },
    getImproperEvidenceList(typeName) {
      // 获取列表数据
      getImproperEvidenceListByCaseNumber({
        caseNumber: this.caseObj.caseNumber
      }).then(res => {
        if (res.data.code === '200') {
          if (!res.data.data) {
            this.evidenceList = []
          } else {
            originList = res.data.data.caseMgtImproperEvidenceVoList
            this.doFilterList(typeName)
          }
        }
      })
    },
    updataList() {
      this.getImproperEvidenceList(this.selectedType)
    },
    // 删除非正常证据
    deleteImproperEvidence(id) {
      this.$confirm('您确定要删除吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          deleteImproperEvidence({
            evidenceAnalysisImproperId: id
          }).then(res => {
            if (res.data.code === '200') {
              // 本地数据同步删除
              originList = _.filter(originList, o => {
                return o.evidenceAnalysisImproperId !== id
              })
              this.updataList()
              this.$message({
                type: 'success',
                message: '删除成功!'
              })
            }
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    },

    editImproperEvidence(obj) {
      this.ee.emit('doEditAsync', obj)
    },
    // 根据证据类型筛选证据
    doFilterList(typeName) {
      this.evidenceList = _.filter(originList, o => {
        return o.improperEvidenceName === typeName
      })
    }
  },
  mounted() {
    this.$watch(
      'selectedType',
      newVal => {
        this.doFilterList(newVal)
      },
      {
        immediate: true
      }
    )

    this.$nextTick(() => {
      if (window.eventEmitter) {
        this.ee = window.eventEmitter
      } else {
        this.ee = window.eventEmitter = new EE()
      }

      ImproperEvidenceAddedCb = data => {
        let selectedTypeArr = _.filter(this.typeList, o => {
          return o.eName === data
        })
        if (selectedTypeArr.length > 0) {
          this.selectedType = selectedTypeArr[0].cName
        }
        this.updataList()
      }
      this.ee.addListener('ImproperEvidenceAdded', ImproperEvidenceAddedCb)
    })

    // 获取非正常证据类别
    getImproperEvidenceType({
      code: 'improper',
      classify: 'evidence'
    }).then(res => {
      if (res.data.code === '200') {
        this.typeList = res.data.data
        this.selectedType = this.typeList[0].cName
        // 获取列表数据
        this.getImproperEvidenceList(this.selectedType)
      }
    })
  },

  destroyed() {
    this.ee.removeListener('ImproperEvidenceAdded', ImproperEvidenceAddedCb)
  }
}
</script>

<style lang="scss" scoped>
.improper-evidence-list-wrap {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  font-size: 14px;

  .list-header {
    background-color: #fff;
    padding: 24px 24px;
  }

  .list-body {
    height: calc(100% - 62px);
    overflow: auto;
    padding: 0 26px;
    .improper-evidence-item {
      border-bottom: 1px solid #e8e8e8;
      margin: 0;
      margin-bottom: 24px;
      padding-bottom: 24px;
      dt {
        position: relative;
        line-height: 1em;
        margin-bottom: 12px;
        font-size: 14px;
        color: #333;
        font-weight: 600;
        span {
          &.title {
            font-weight: bold;
            color: #333;
          }

          &.op-btns-wrap {
            position: absolute;
            top: -15px;
            right: 15px;

            .el-button {
              min-width: 0;
              line-height: 1.5em;
            }
          }
        }
      }

      dd {
        margin: 0;
        margin-bottom: 24px;
        color: #666;
        font-size: 14px;
        font-weight: 400;
        &.desc-item {
          display: inline-block;
          margin-right: 5em;
          margin-bottom: 0;
          &:last-child {
            margin-right: 0;
          }
          i {
            display: inline-block;
            width: 8px;
            height: 8px;
            background-color: #41a0fa;
            border-radius: 50%;
            margin-right: 8px;
          }
        }

        &.text {
          line-height: em;
        }
      }
    }
  }
}

.no-result {
  text-align: center;
  margin-top: 200px;
  img {
    margin-bottom: 10px;
  }
  p {
    margin: 0;
  }
}
</style>

<style lang="scss">
.el-radio-button--medium .el-radio-button__inner {
  padding: 12px !important;
}
</style>



