<template>
<div>
<div v-if="noEvidenceChainsData" class="no-result">
        <img src="../assets/img/no_result.png" alt="" />
        <p style="text-align: center;">证据链暂无数据！</p>
</div>
  <div v-if="!noEvidenceChainsData" style="position:absolute;top:0;left:0;display:block;width:100%;height:100%;" id="topoCanvas"></div>
  <div id="temp-html2canvas-div" style="text-align: justify;z-index:-9999;position: absolute;"></div>
  <div v-loading="loading" v-if="loading" style="position:absolute;top:0;left:0;display:block;width:100%;height:100%;"></div>
</div>
</template>
<script>
import { getFactConfirmListByCaseNumber } from '@/api/root'
// import html2canvas from 'html2canvas'

import _ from 'lodash'
// step3 共享数据
import { store as step3SharedStore } from '@/utils/global-store'
// import * as d3 from 'd3'

let Q = require('yofc-qunee')
let graph = null
let html2canvasPromiseArr = []

function getHTMLSVGImage(arg, callback) {
  let html = _.truncate(arg.html, { length: 700 })
  let height =
    Math.ceil(html.length / 40) * 12 + 115 + arg.evidenceSourceLength * 12

  let paddingBot =
    arg.evidenceSourceLength * 1 === 0 ? 15 : arg.evidenceSourceLength * 18
  let deferred = new Promise(function(resolve, reject) {
    let data = `<svg xmlns="http://www.w3.org/2000/svg" width="500" height="${height}">
      <foreignObject width="100%" height="100%">
      <p xmlns="http://www.w3.org/1999/xhtml" style="font-size:12px;color:#999;padding:15px;text-align:justify;margin:0;padding-bottom:${paddingBot}px">
      <![CDATA[
        ${html}
      ]]>
      </p>
      </foreignObject>
      </svg>`

    let DOMURL = window.URL || window.webkitURL || window
    let img = new Image()
    let svg = new Blob([data], { type: 'image/svg+xml' })
    let url = DOMURL.createObjectURL(svg)
    img.onload = function(evt) {
      callback(evt.target, resolve)
      DOMURL.revokeObjectURL(url)
    }
    img.src = url
  })
  return deferred
}

// 运行一下函数的时候，graph有引用
function createNode(text, x, y, isRoot, data) {
  let node = null
  if (isRoot && text && text !== '') {
    text = text.split('').join('\n')
  }

  // if (data && text.length > 144) {
  if (data) {
    node = graph.createText('', x, y)
    let arg = {
      html: text,
      evidenceSourceLength: data.evidenceSourceArrLength
    }
    html2canvasPromiseArr.push(
      getHTMLSVGImage(arg, function(image, resolveCb) {
        node.image = {
          cacheable: false,
          image: image,
          width: image.width,
          height: image.height,
          draw: function(g) {
            g.drawImage(this.image, 0, 0)
            resolveCb()
          }
        }
        node.setStyle(Q.Styles.BORDER, 1)
        node.setStyle(Q.Styles.BORDER_COLOR, '#efefef')
        node.setStyle(Q.Styles.BORDER_RADIUS, 0)
      })
    )
  } else {
    if (isRoot) {
      node = graph.createText(text, x, y)
    } else {
      node = graph.createText(text.replace(/[\r\n]/g, ''), x, y)
    }
  }

  node.movable = false
  node.setStyle(Q.Styles.LABEL_FONT_SIZE, 16)
  node.setStyle(Q.Styles.LABEL_COLOR, '#999999')
  node.setStyle(Q.Styles.LABEL_BACKGROUND_COLOR, '#ffffff')
  node.setStyle(Q.Styles.LABEL_BORDER, 1)
  node.setStyle(Q.Styles.LABEL_BORDER_STYLE, '#e7e7e7')
  node.setStyle(Q.Styles.LABEL_RADIUS, 0)
  node.setStyle(Q.Styles.LABEL_PADDING, new Q.Insets(15, 15))
  return node
}

function createEdge(node1, node2) {
  let edge = graph.createEdge(node1, node2)
  edge.setStyle(Q.Styles.ARROW_TO, false)
  edge.edgeType = Q.Consts.EDGE_TYPE_ORTHOGONAL_HORIZONTAL
  edge.setStyle(Q.Styles.EDGE_COLOR, '#e7e7e7')
  edge.setStyle(Q.Styles.EDGE_CORNER_RADIUS, 0)
  return edge
}
// 摘录来源节点
function createLabel(node, text, offsetY, offsetX) {
  var label = new Q.LabelUI(text)
  label.position = Q.Position.RIGHT_BOTTOM
  label.anchorPosition = Q.Position.RIGHT_BOTTOM
  label.color = '#54a7f7'
  if (offsetY) {
    label.offsetY = offsetY
  }
  if (offsetX) {
    label.offsetX = offsetX
  }
  node.addUI(label)
  return label
}

// 判断是否应该绘制固定分类的节点
function canDrawClassifyNode(evidenceList) {
  var result = true
  var temp = []
  if (evidenceList.length === 0) {
    result = false
    return result
  }

  _.forEach(evidenceList, o => {
    if (
      o.evidenceApproveContent !== '' ||
      o.evidenceExtractContent !== '' ||
      o.evidenceExtractTitle !== ''
    ) {
      temp.push(o)
    }
  })

  if (temp.length === 0) {
    result = false
  } else {
    result = true
  }
  return result
}
export default {
  props: {
    caseObj: {
      type: Object,
      required: true
    }
  },
  name: '',
  data() {
    return {
      loading: false,
      noEvidenceChainsData: false,
      // 共享状态数据
      step3SharedStore: step3SharedStore
    }
  },
  computed: {},
  components: {},
  methods: {
    initMindMap(data) {
      this.loading = true
      graph = new Q.Graph('topoCanvas')
      let styles = (graph.styles = {})
      styles[Q.Styles.SELECTION_TYPE] = Q.Consts.SELECTION_TYPE_BORDER_RECT
      styles[Q.Styles.SELECTION_COLOR] = '#54a7f7'
      styles[Q.Styles.SELECTION_BORDER] = 0
      styles[Q.Styles.SELECTION_PADDING] = new Q.Insets(12, 16)
      // 不显示tips
      graph.enableTooltip = false
      // 树不可编辑
      graph.editable = false
      // 不允许调整图元大小
      graph.isResizable = function() {
        return false
      }
      // 不允许旋转图元
      graph.isRotatable = function() {
        return false
      }

      // 定制节点点击事件
      graph.addCustomInteraction({
        onmousemove: evt => {
          let data = graph.getElementByMouseEvent(evt)
          if (!data) {
            return
          }
          if (!data.get('hasSource')) {
            return
          }
          if (data instanceof Q.Node) {
            var target = graph.hitTest(evt)
            if (target instanceof Q.LabelUI) {
              // 如果单击的是摘录文字部分，则不跳转
              if (target.color === '#54a7f7') {
                graph.cursor = 'pointer'
              }
            } else {
              graph.cursor = null
            }
          }
        },
        onclick: evt => {
          let data = graph.getElementByMouseEvent(evt)
          if (!data) {
            return
          }
          if (!data.get('hasSource')) {
            return
          }
          if (data instanceof Q.Node) {
            var target = graph.hitTest(evt)
            if (target instanceof Q.LabelUI) {
              // 如果单击的是摘录文字部分，则不跳转
              if (target.color === '#999999') {
                return
              }
              let factConfirmId = data.get('factConfirmId')
              let evidenceFixedClassifyId = data.get('evidenceFixedClassifyId')
              let factConfirmEvidenceId = data.get('factConfirmEvidenceId')
              let ocrFileId = target.ocrFileId
              this.$router.push({
                name: 'step3',
                params: {
                  factConfirmId,
                  evidenceFixedClassifyId,
                  factConfirmEvidenceId,
                  ocrFileId
                }
              })
            }
          }
        }
      })
      // 绘制根节点
      let root = createNode(this.caseObj.caseName, 0, 0, true)
      _.forEach(data.caseMgtFactConfirmVoList, o => {
        let factFindingTitleNode = createNode(o.factFindingTitle, 0, 0)
        createEdge(root, factFindingTitleNode)

        if (
          o.caseMgtEvidenceClassifyVoList &&
          o.caseMgtEvidenceClassifyVoList.length > 0
        ) {
          _.forEach(o.caseMgtEvidenceClassifyVoList, i => {
            if (canDrawClassifyNode(i.caseMgtFactConfirmEvidenceVoList)) {
              let evidenceFixedClassifyNameNode = createNode(
                i.evidenceFixedClassifyName,
                0,
                0
              )
              createEdge(factFindingTitleNode, evidenceFixedClassifyNameNode)

              if (
                i.caseMgtFactConfirmEvidenceVoList &&
                i.caseMgtFactConfirmEvidenceVoList.length > 0
              ) {
                _.forEach(i.caseMgtFactConfirmEvidenceVoList, k => {
                  let evidenceExtractTitleNode
                  let evidenceApproveContentNode
                  let evidenceExtractContentNode

                  if (
                    !k.evidenceExtractTitle &&
                    !k.evidenceExtractContent &&
                    !k.evidenceApproveContent
                  ) {
                    return
                  }
                  // 绘制一个证据空节点
                  let emptyNode = createNode('', 0, 0)
                  createEdge(evidenceFixedClassifyNameNode, emptyNode)

                  if (k.evidenceExtractTitle) {
                    // 证据摘录标题
                    evidenceExtractTitleNode = createNode(
                      k.evidenceExtractTitle,
                      0,
                      0
                    )
                    evidenceExtractTitleNode.set('hasSource', false)
                    createEdge(emptyNode, evidenceExtractTitleNode)
                  }
                  // 证据摘要节点
                  if (k.evidenceExtractContent) {
                    // 证据节点需要设置自定义属性，供跳转到step3传参
                    evidenceExtractContentNode = createNode(
                      k.evidenceExtractContent,
                      0,
                      0,
                      false,
                      {
                        evidenceExtractContent: k.evidenceExtractContent,
                        evidenceSourceArrLength:
                          k.caseMgtFactConfirmEvidenceSourceVoList.length
                      }
                    )

                    evidenceExtractContentNode.set(
                      'factConfirmId',
                      k.factConfirmId
                    )
                    evidenceExtractContentNode.set(
                      'evidenceFixedClassifyId',
                      k.evidenceFixedClassifyId
                    )
                    evidenceExtractContentNode.set(
                      'factConfirmEvidenceId',
                      k.factConfirmEvidenceId
                    )

                    evidenceExtractContentNode.set('hasSource', false)
                    if (k.caseMgtFactConfirmEvidenceSourceVoList.length) {
                      evidenceExtractContentNode.setStyle(
                        Q.Styles.LABEL_ALIGN_POSITION,
                        Q.Position.LEFT_TOP
                      )
                    }

                    evidenceExtractContentNode.setStyle(Q.Styles.LABEL_SIZE, {
                      width: k.evidenceExtractContent.length * 16 + 30,
                      height:
                        16 +
                        k.caseMgtFactConfirmEvidenceSourceVoList.length * 16
                    })

                    createEdge(emptyNode, evidenceExtractContentNode)
                  }

                  if (k.evidenceApproveContent) {
                    // 证实节点
                    evidenceApproveContentNode = createNode(
                      k.evidenceApproveContent,
                      0,
                      0,
                      false,
                      {
                        evidenceExtractContent: k.evidenceApproveContent,
                        evidenceSourceArrLength: 0
                      }
                    )
                    evidenceApproveContentNode.set('hasSource', false)

                    evidenceApproveContentNode.setStyle(Q.Styles.LABEL_SIZE, {
                      width: k.evidenceApproveContent.length * 16 + 30,
                      height:
                        16 +
                        k.caseMgtFactConfirmEvidenceSourceVoList.length * 16
                    })

                    createEdge(emptyNode, evidenceApproveContentNode)
                  }

                  if (
                    k.caseMgtFactConfirmEvidenceSourceVoList &&
                    k.caseMgtFactConfirmEvidenceSourceVoList.length > 0
                  ) {
                    evidenceExtractContentNode.set('hasSource', true)
                    _.forEach(
                      k.caseMgtFactConfirmEvidenceSourceVoList,
                      (j, jindex) => {
                        let labelNode = createLabel(
                          evidenceExtractContentNode,
                          j.ocrFileName,
                          -1 * jindex * 20 - 15,
                          -15
                        )
                        // 自己加一个属性上去
                        labelNode.ocrFileId = j.ocrFileId
                      }
                    )
                  }
                })
              }
            }
          })
        }
      })

      Promise.all(html2canvasPromiseArr).then(() => {
        // 自动布局
        let layouter = new Q.TreeLayouter(graph)
        layouter.parentChildrenDirection = Q.Consts.DIRECTION_RIGHT
        layouter.layoutType = Q.Consts.LAYOUT_TYPE_EVEN_VERTICAL
        layouter.doLayout({
          callback: () => {
            this.loading = false
            graph.zoomToOverview()
            html2canvasPromiseArr = []
          }
        })
      })
    },
    getMindMapDataAsync() {
      this.loading = true
      let caseMgtFactConfirmVoList = this.step3SharedStore.step3AllDatas
      let p = new Promise((resolve, reject) => {
        if (_.isEmpty(caseMgtFactConfirmVoList)) {
          this.loading = false
          this.noEvidenceChainsData = true
          reject(new Error('没有缓存数据可用'))
        } else {
          this.noEvidenceChainsData = false
          this.loading = false
          resolve({ caseMgtFactConfirmVoList })
        }
      })
      return p
    },
    getMindMapData() {
      this.loading = true
      let p = new Promise((resolve, reject) => {
        getFactConfirmListByCaseNumber({
          caseNumber: this.caseObj.caseNumber
        }).then(res => {
          if (res.data.code === '200') {
            if (res.data.data.caseMgtFactConfirmVoList.length > 0) {
              this.noEvidenceChainsData = false
              this.loading = false
              resolve(res.data.data)
            } else {
              this.loading = false
              this.noEvidenceChainsData = true
            }
          } else {
            this.loading = false
            reject(res.data.msg)
          }
        })
      })
      return p
    }
  },
  destroyed() {
    graph.destroy()
  },
  mounted() {
    // Edge Blob polyfill https://developer.mozilla.org/en-US/docs/Web/API/HTMLCanvasElement/toBlob
    if (!HTMLCanvasElement.prototype.toBlob) {
      Object.defineProperty(HTMLCanvasElement.prototype, 'toBlob', {
        value: function(callback, type, quality) {
          let canvas = this
          setTimeout(function() {
            let binStr = atob(canvas.toDataURL(type, quality).split(',')[1])
            let len = binStr.length
            let arr = new Uint8Array(len)
            for (var i = 0; i < len; i++) {
              arr[i] = binStr.charCodeAt(i)
            }
            callback(new Blob([arr], { type: type || 'image/png' }))
          })
        }
      })
    }
    this.getMindMapDataAsync()
      .then(data => {
        this.initMindMap(data)
      })
      .catch(error => {
        console.log('失败了:' + error)
        this.getMindMapData()
          .then(data => {
            this.initMindMap(data)
          })
          .catch(error => {
            console.log('失败了:' + error)
          })
      })
  }
}
</script>
<style lang="scss">
.no-result {
  text-align: center;
  margin-top: 200px;
  img {
    margin-bottom: 10px;
  }
  p {
    margin: 0;
  }
}
</style>



