<template>
  <div class="step-wrap" id="step4">

    <el-tabs v-model="currentTabName" class="custom-tabs-wrap">
      <el-tab-pane :label="editableTabsList[0].title" :name="editableTabsList[0].name">
        <div class="step-body-wrap">
          <keep-alive>
          <evidence-chains :caseObj="caseObj"></evidence-chains>
          </keep-alive>
        </div>
      </el-tab-pane>
      <el-tab-pane :label="editableTabsList[1].title" :name="editableTabsList[1].name">
        <span slot="label">{{editableTabsList[1].title}}<el-badge :value="riskDataTotal" :max="10" class="item" v-if="riskDataTotal"/></span>
        <div class="step-body-wrap">
          <keep-alive>
          <risk-warning-list :caseObj="caseObj" :initData="riskDataList"></risk-warning-list>
          </keep-alive>
        </div>
      </el-tab-pane>
      <el-tab-pane :label="editableTabsList[2].title" :name="editableTabsList[2].name">
        <div class="step-body-wrap">
          <keep-alive>
          <improper-evidence-list :caseObj="caseObj"></improper-evidence-list>
          </keep-alive>
        </div>
      </el-tab-pane>
    </el-tabs>
    <div class="step-footer-wrap">
      <!-- <el-button size="small">保存</el-button> -->
      <!-- <el-button size="small">导出</el-button> -->
      <el-button size="small" @click="prev">上一步</el-button>
      <el-button size="small" type="primary" @click="next">下一步</el-button>
    </div>
  </div>
</template>

<script>
import RiskWarningList from '../../../components/RiskWarningList.vue'
import ImproperEvidenceList from '../../../components/ImproperEvidenceList.vue'
// import EvidenceChainsV2 from '../../../components/EvidenceChainsV2.vue'
import EvidenceChains from '../../../components/EvidenceChains.vue'
import { getRiskTipsList } from '@/api/root.js'
import EE from 'events'

let updateRiskListCb
export default {
  name: 'step4',
  props: {
    caseObj: {
      type: Object,
      required: true
    }
  },
  components: {
    RiskWarningList,
    ImproperEvidenceList,
    EvidenceChains
    // EvidenceChainsV2
  },
  data() {
    return {
      msg: 'step4',
      tabIndex: 1,
      // 是否可以关闭tab
      closable: false,
      currentTabName: '1',
      labelPosition: 'top',
      riskDataTotal: 0,
      riskDataList: [],
      editableTabsList: [
        {
          title: '证据链',
          name: '1',
          contentForm: {
            basicInfo: ''
          }
        },
        {
          title: '风险提示',
          name: '2',
          contentForm: {
            basicInfo: ''
          }
        },
        {
          title: '瑕疵或非法证据',
          name: '3',
          contentForm: {
            basicInfo: ''
          }
        }
      ]
    }
  },
  methods: {
    next() {
      this.$router.push({ name: 'step5' })
    },
    prev() {
      this.$router.push({ name: 'step3' })
    },
    update() {
      // 获取风险项列表
      getRiskTipsList({
        caseNumber: this.caseObj.caseNumber
      }).then(res => {
        if (res.data.code === '200') {
          this.riskDataList = res.data.data.rules
          this.riskDataTotal = res.data.data.total
        }
      })
    }
  },
  mounted() {
    // 事件总线
    if (window.eventEmitter) {
      this.ee = window.eventEmitter
    } else {
      this.ee = window.eventEmitter = new EE()
    }

    this.update()

    updateRiskListCb = data => {
      this.update()
    }
    // 监听事件
    this.ee.addListener('updateRiskList', updateRiskListCb)
  },
  destroyed() {
    this.ee.removeListener('updateRiskList', updateRiskListCb)
    // console.log('step4 destoryed')
  }
}
</script>
<style lang="scss">
#step4 {
  > .custom-tabs-wrap.el-tabs {
    > .el-tabs__header {
      padding-left: 24px;

      > .el-tabs__nav {
        padding: 0 8px;
      }

      > .el-tabs__active-bar {
        padding: 0 8px;
      }

      .el-tabs__item {
        color: #333;
        font-weight: 600;
        &:hover,
        &.is-active {
          color: #42a0f8;
        }
      }
    }
  }

  &.step-wrap {
    .step-body-wrap {
      padding: 24px;
    }
  }
}
</style>



