<template>
<div class="risk-list-wrap">
    <dl v-for="item in initData" :key="item.index">
        <dt>{{item.title}}</dt>
        <dd>
            <span v-for="subItem in item.block" :key="subItem.index"><i class="iconfont" v-html="subItem.icon"></i>{{subItem.label}}：{{subItem.descr}}</span>
        </dd>
        <dd class="btns-wrap">
            <span>
                <el-button type="text" @click="switchPdfByOcrfileId(item.source)" v-if="item.source">查看卷宗</el-button>
                <el-button :class="{isIgnore:item.ignore,notIgnore:!item.ignore}" type="text" @click="setIgnore({type:item.type,hinge:item.hinge,ignore:item.ignore})">{{showActionDesc(item.ignore)}}</el-button>
            </span>
        </dd>
    </dl>
    <div v-if="initData.length == 0" class="no-result">
        <img src="../assets/img/no_result.png" alt="" />
        <p style="text-align: center;">该列表暂无数据！</p>
    </div>
</div>
</template>
<script>
import { setIgnoreRiskTip } from '@/api/root'
import EE from 'events'

export default {
  props: {
    caseObj: {
      type: Object,
      required: true
    },
    initData: {
      type: Array,
      required: true
    }
  },
  data() {
    return {}
  },
  name: '',
  computed: {},
  components: {},
  methods: {
    // 显示“忽略” 和 “已忽略” 标记
    showActionDesc(bool) {
      return bool ? '激活' : '忽略'
    },
    // 忽略风险项
    setIgnore(data) {
      // if (data.ignore) {
      //   return
      // }
      setIgnoreRiskTip({
        caseNumber: this.caseObj.caseNumber,
        type: data.type,
        ignore: !data.ignore,
        hinge: data.hinge
      }).then(res => {
        if (res.data.code === '200') {
          // 触发列表刷新
          this.ee.emit('updateRiskList')
        } else {
          this.$message.error(res.data.msg)
        }
      })
    },
    // 查看指定风险项目的pdf出处（左侧切换到对应的pdf）
    switchPdfByOcrfileId(data) {
      let ocrfileId
      if (!data) {
        return
      }
      if (data.length > 0) {
        ocrfileId = data[0]
      }
      this.ee.emit('switchPdfByOcrfileIdEvent', ocrfileId)
    },
    // 刷新列表
    update() {}
  },
  mounted() {
    // 事件总线
    if (window.eventEmitter) {
      this.ee = window.eventEmitter
    } else {
      this.ee = window.eventEmitter = new EE()
    }
  }
}
</script>

<style lang="scss" scoped>
.isIgnore.el-button--text {
  color: #23ca85;
}
.notIgnore.el-button--text {
  color: #f05468;
}
.risk-list-wrap {
  padding: 0;
  dl {
    border-bottom: 1px solid #e8e8e8;
    position: relative;
    line-height: 2;
    margin: 10px 0px;
    padding-bottom: 0;

    &:nth-child(1) {
      dt {
      }
      margin-top: -10px !important;
    }
    dt {
      color: #333;
      font-size: 14px;
      font-weight: 400;
      line-height: 1em;
      margin-bottom: 16px;
      margin-top: 16px;
    }
    dd {
      color: #999;
      font-size: 14px;
      font-weight: 400;
      margin: 0;
      margin-bottom: 16px;
      line-height: 1em;
      &.btns-wrap {
        position: absolute;
        right: 0;
        top: 2px;
      }

      span {
        margin-right: 32px;
        i {
          margin-right: 8px;
          position: relative;
          top: 1px;
        }
      }
    }
  }
}

.no-result {
  text-align: center;
  margin-top: 200px;
  img {
    margin-bottom: 10px;
  }
  p {
    margin: 0;
  }
}
</style>



